@font-face {
  font-family: Avenir;
  src: url(./fonts/Avenir.ttc);
  font-weight: 400;
}

@font-face {
  font-family: Avenir;
  src: url(./fonts/Avenir-Heavy.ttf);
  font-weight: 700;
}

@font-face {
  font-family: Avenir;
  src: url(./fonts/Avenir-Book.ttf);
  font-weight: 300;
}

body {
  font-family: Avenir, sans-serif;
  font-size: 18px;
  box-sizing: border-box;
}

form {
  position: relative;
  margin-bottom: 20px;
  margin-top: 20px;
}

.search {
  font-family: Avenir, sans-serif;
  font-weight: 300;
  width: 100%;
  margin: 0 auto;
  font-size: 16px;
  line-height: 22px;
  padding: 23px 62px;
  background: url(./images/Search.svg) no-repeat 26px center;
  border: 1px solid #e1e7ed;
  box-shadow: 0 1px 8px rgba(61, 78, 97, 0.1);
  border-radius: 4px;
}

.search:hover {
  box-shadow: 0 3px 8px rgba(61, 78, 97, 0.2);
}

.search:focus {
  background: url(./images/Search.svg) no-repeat 26px center,
    linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
  border-radius: 3px;
}

.search__small {
  background-position: 12px center;
  background-size: 11px;
  padding: 10px 33px 11px;
  font-size: 14px;
  line-height: 19px;
}

.search__small:focus {
  background: url(./images/Search.svg) no-repeat 12px center,
    linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
  background-size: 11px;
}
